  font-family: 'Lemon/Milk';
  font-weight: bold;
  src: local('Lemon/Milk'),
      url('fonts/LemonMilkbold.otf') format('opentype');
}

body {
  font-family: 'Lemon/Milk';
}

body {
  margin: 0;
  padding: 0;
}



